"use client";

import React, { useState, useEffect } from 'react';
import { Star, Heart, Sparkles, Mail, Lock, Plus, Edit2, Trash2, LogOut } from 'lucide-react';

export default function Page() {
  const [view, setView] = useState("home");
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [currentCartinha, setCurrentCartinha] = useState(null);
  const [searchId, setSearchId] = useState("");
  const [loading, setLoading] = useState(false);

  // mock API local s√≥ pra funcionar por enquanto
  const mockAPI = {
    cartinhas: [],

    async login(email, password) {
      return email === "contato.shai@proton.me" && password === "OiOi()()";
    },

    async getCartinha(id) {
      return this.cartinhas.find(c => c.id === id);
    },

    async createCartinha(c) {
      this.cartinhas.push(c);
      return c;
    },

    async getAllCartinhas() {
      return [...this.cartinhas];
    },

    async updateCartinha(id, data) {
      const i = this.cartinhas.findIndex(c => c.id === id);
      if (i !== -1) this.cartinhas[i] = { ...this.cartinhas[i], ...data };
      return this.cartinhas[i];
    },

    async deleteCartinha(id) {
      const i = this.cartinhas.findIndex(c => c.id === id);
      if (i !== -1) this.cartinhas.splice(i, 1);
    }
  };

  const handleSearchCartinha = async () => {
    setLoading(true);
    const c = await mockAPI.getCartinha(searchId.trim());
    setLoading(false);

    if (c) {
      setCurrentCartinha(c);
      setView("cartinha");
    } else {
      setView("notfound");
    }
  };

  const handleLogout = () => {
    setIsLoggedIn(false);
    setView("home");
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-purple-50">

      {view === "home" && (
        <HomePage 
          searchId={searchId}
          setSearchId={setSearchId}
          onSearch={handleSearchCartinha}
          loading={loading}
          onAdminClick={() => setView("login")}
        />
      )}

      {view === "notfound" && <NotFoundPage onBack={() => setView("home")} />}

      {view === "cartinha" && currentCartinha && (
        <CartinhaPage
          cartinha={currentCartinha}
          onBack={() => {
            setView("home");
            setSearchId("");
            setCurrentCartinha(null);
          }}
        />
      )}

      {view === "login" && !isLoggedIn && (
        <LoginPage
          onLogin={() => {
            setIsLoggedIn(true);
            setView("admin");
          }}
          onBack={() => setView("home")}
          mockAPI={mockAPI}
        />
      )}

      {view === "admin" && isLoggedIn && (
        <AdminPanel mockAPI={mockAPI} onLogout={handleLogout} />
      )}
    </div>
  );
}

/*  
===========================================================
A PARTIR DAQUI FICAM OS OUTROS COMPONENTES
===========================================================
*/

function HomePage({ searchId, setSearchId, onSearch, loading, onAdminClick }) {
  return (
    <div className="p-10">
      <h1 className="text-4xl font-bold">Cartinha Digital üíå</h1>

      <input 
        value={searchId}
        onChange={e => setSearchId(e.target.value)}
        placeholder="Digite o ID"
        className="border p-2 mt-4 w-full"
      />

      <button 
        onClick={onSearch}
        className="bg-blue-500 text-white p-3 rounded mt-4 w-full">
        Abrir Cartinha
      </button>

      <button 
        onClick={onAdminClick}
        className="text-sm text-gray-500 mt-4 block text-center">
        √Årea Admin
      </button>
    </div>
  );
}

function NotFoundPage({ onBack }) {
  return (
    <div className="p-10 text-center">
      <h2 className="text-2xl font-bold">ID n√£o encontrado üòÖ</h2>
      <button onClick={onBack} className="mt-4 p-3 bg-blue-500 text-white rounded">
        Voltar
      </button>
    </div>
  );
}

function CartinhaPage({ cartinha, onBack }) {
  return (
    <div className="p-10">
      <button onClick={onBack} className="mb-4">‚Üê Voltar</button>
      <h1 className="text-3xl font-bold mb-4">Para {cartinha.nome}</h1>
      <p className="text-lg">{cartinha.texto}</p>
    </div>
  );
}

function LoginPage({ onLogin, onBack, mockAPI }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const tryLogin = async () => {
    const ok = await mockAPI.login(email, password);
    if (ok) onLogin();
    else alert("Login incorreto");
  };

  return (
    <div className="p-10">
      <button onClick={onBack}>‚Üê Voltar</button>

      <input 
        type="email" 
        placeholder="Email"
        className="border p-2 w-full mt-4"
        value={email}
        onChange={e => setEmail(e.target.value)}
      />

      <input 
        type="password"
        placeholder="Senha"
        className="border p-2 w-full mt-4"
        value={password}
        onChange={e => setPassword(e.target.value)}
      />

      <button 
        onClick={tryLogin}
        className="p-3 bg-blue-500 text-white w-full rounded mt-4">
        Entrar
      </button>
    </div>
  );
}

function AdminPanel({ mockAPI, onLogout }) {
  const [cartinhas, setCartinhas] = useState([]);
  const [newNome, setNewNome] = useState("");
  const [newId, setNewId] = useState("");
  const [newTexto, setNewTexto] = useState("");

  const load = async () => {
    setCartinhas(await mockAPI.getAllCartinhas());
  };

  useEffect(() => {
    load();
  }, []);

  const criar = async () => {
    await mockAPI.createCartinha({
      id: newId,
      nome: newNome,
      texto: newTexto
    });
    load();
  };

  return (
    <div className="p-10">
      <button onClick={onLogout} className="mb-6">Sair</button>

      <h2 className="font-bold text-xl">Criar nova cartinha</h2>

      <input 
        placeholder="ID"
        className="border p-2 w-full mt-2"
        value={newId}
        onChange={e => setNewId(e.target.value)}
      />

      <input 
        placeholder="Nome"
        className="border p-2 w-full mt-2"
        value={newNome}
        onChange={e => setNewNome(e.target.value)}
      />

      <textarea 
        placeholder="Texto"
        className="border p-2 w-full mt-2"
        value={newTexto}
        onChange={e => setNewTexto(e.target.value)}
      />

      <button 
        onClick={criar}
        className="bg-green-500 text-white p-3 w-full rounded mt-4">
        Salvar
      </button>

      <h2 className="font-bold text-xl mt-8">Cartinhas criadas</h2>

      {cartinhas.map(c => (
        <div key={c.id} className="p-3 border rounded mt-2">
          <p><b>ID:</b> {c.id}</p>
          <p><b>Nome:</b> {c.nome}</p>
        </div>
      ))}
    </div>
  );
}
